    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
body {
  background-color: #F1E3D7;      
  color:#704736;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-style: normal;
}

#wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}


/**** Header Section ****/ 
  
.site-header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.page-heading {
  padding: 30px;
}

.page-heading h1 {
  font-size: 32px; 
}

.header-pic {
  width: 100%; 
  display: flex;
  justify-content: center;
}

.header-pic img {
  width: 100%;
  display: block; 
}

/******** Main section start ********/ 

.main {
  width: 100%;
  display: flex;
  flex-wrap: wrap; 
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  margin-bottom:150px;
}

/**** Search section ****/ 

.search-section {
  width: 95%; 
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-input-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  height: 35px;
  border-radius: 25px; 
  background-color: #fff;
}

.search-input{
  width: 100%;
  height: 25px;
  border-radius: 25px;
  border: 0;
  padding-left: 5px;
  margin-left: 5px;
  background-color: #fff;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration { display: none; }

.search-button-container {
  margin-left: 10px;
}

input:active {
  border: 1px solid #794727; 
}

.btn {
  height: 35px;
  font-size: 14px; 
  font-family: "Poppins", sans-serif;
  border: 1px solid #CE926C;
  border-radius: 10px;
  color: #fff;
  background-color:#CE926C;
  padding: 0 10px;
  cursor: pointer;
}

.btn:hover {
  
  background-color: #B0745B;
  border: 1px solid #B0745B; 
  color:#fff; 
}

.btn:active {
  border: 1px solid #CE926C;
  background-color: #CE926C;
}


/**** Loading message and animation ****/ 

.loading {
  width: 100%;
  font-weight: 500;
  display: none; 
  font-size: 24px;
  color: #81523e;
  text-align: center;
  margin-top: 20px;
}

.loading.show {
  display: block; 
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading::after {
  content: '';
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #704736;
  border-radius: 50%;
  animation: spin 2s linear infinite;
}


/**** Results container ****/

.results-container {
  width: 100%;
  display: flex;
  max-width: 1500px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px; 
}


.book-item {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #E1BD9E;
  border-radius: 5px; 
  margin-top: 30px;
  height: 400px;
  position: relative;
}

  
.book-cover {
  width: 30%;
  height: auto;
  margin-bottom: 10px;
  padding-top: 10px;
  padding-left: 10px;
}

.book-info {
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
}

.authors {
  font-weight: 400; 
}

/**** Description Button ****/

.description-button-wrapper {
  width: 100%;
  display: flex; 
  justify-content: center;
  position: absolute; 
  bottom: 0; 
  margin-bottom: 50px;
}

.description-button {
  width: 50%;
}

/**** Book Description ****/ 

.book-title-description {
  font-size: 28px;
  font-weight: 600;
  font-style: italic;
  margin-bottom: 10px;
  text-align: center;
  letter-spacing: 2px;
}

.book-description-container {
  width: 90%; 
  border: 3px solid #B0745B; 
  display: flex;
  flex-direction: column;
  max-width: 1000px;
  border-radius: 5px; 
  padding: 20px; 
}

.p-description {
  font-style: italic;
  text-align: justify;
}

.back-button {
  margin-top: 20px;

}

/**** Error message ****/

.error-message { 
  color: #81523e;
  font-weight: 500;
  font-style: italic;
  letter-spacing: 2px;
  font-size: 24px;
}



/**** Footer Section ****/

.site-footer{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: fixed;
  bottom: 0;
  background-color: #F1E3D7;
}

.social-icons li {
  display: inline;
  margin-right: 18px;
}

.social-icons li:last-of-type {
  margin-right: 0px;
}

.footer-items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  align-items: center;
  padding-top: 10px;
}

.footer-paragraph {
  font-size: 14px;
  padding: 10px;
  
}


/**** media query for TABLET or medium devices ****/

@media only screen and (min-width: 670px) and (max-width: 991px) {
  .page-heading h1 {
    font-size: 44px;
  }
  
  .header-pic img {
    max-width: 600px;
  }
  
  .search-input-container {
    width: 300px; 
    height: 40px;
  }

  .search-input{
    height: 30px;
  }

  .btn {
    width: 100%;
    height: 40px; 
    padding: 0 20px;
  }

  .site-footer {
    margin-top: 400px;
  }

  .book-item {
    width: 100%;
    flex-direction: row;
    background-color: #E1BD9E;
    justify-content: center;
    border-radius: 5px; 
    margin-top: 30px;
    height: 300px;
    position: relative;
  }

  .book-info {
    box-sizing: border-box;
    text-align: center;
    width: 100%;
    padding-top: 40px;
  }

.description-button-wrapper {
    width: 35%;
    display: flex; 
    justify-content: center;
    position: absolute;
    left: 45%;
    bottom: 10%;
  }
}


/**** media query for Laptops or large devices ****/

@media only screen and (min-width: 992px) {
  .page-heading h1 {
    font-size: 44px; 
  }

  .header-pic img {
    max-width: 750px; 
  }

  .search-input-container {
    width: 350px; 
    height: 40px; 
  }

  .search-input{
    height: 30px;
  }

  .btn {
    height: 40px; 
    padding: 0 20px;
  }

  .footer-items {
    flex-direction: row;
    justify-content: space-between;
  }
  
  .results-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .book-item {
    width: calc(30% - 20px); /* Divide lo spazio in due colonne, con uno spazio tra di esse */
    margin-bottom: 20px;
    box-sizing: border-box;
    background-color: #E1BD9E;
    margin-left: 10px;
    border-radius: 5px; 
    margin-top: 30px;
    height: 400px;
    position: relative;
    box-sizing: border-box;
}

  .book-cover {
    height: auto;
    margin-bottom: 10px;
  }
  
  .book-info {
    text-align: left;
    border-radius: 0;
}
  .description-button-wrapper {
    width: 100%;
    display: flex; 
    flex-direction: row;
    justify-content: center;
    position: absolute; 
    bottom: 0; 
    margin-bottom: 15px;
  }
}
